import Icon24List from '@vkontakte/icons/dist/24/list';
import piter from '../img/nov.jpg';
import '@happysanta/vk-app-ui/dist/vkappui.css'
import { Notify, strong } from "@happysanta/vk-app-ui"
const Stor = ({ id, back, top, openProfile, go }) => {
    return (
        <Panel id={id}>
            <PanelHeader
                left={<PanelHeaderBack onClick={back} />}
            >
                История
            </PanelHeader>
           <Group>
      <Div>
        Первый в России метрополитен был - Московский. 
        Чуть более восьмидесяти лет назад метрополитен появился и в нашей стране. Событие это произошло 15 мая 1935 года в Москве. Как известно, прокладка линий метро рентабельна лишь в городах с миллионным населением и избытком транспорта. Именно в этот период в Москве происходит транспортный подъём. 
      Известно, что самое первое предложение прокладки подземной железной дороги было выдвинуто в 1875 инженером Василием Титовым. Но предложение осталось предложением - против спуска под землю радикально выступила Православная церковь, да и экономического смысла правительство в этом не увидело. Тем не менее, это предложение было не единственным. В период с 1897 года до начала XX века правительству города поступало ещё несколько предложений. Серьезный проект строительства подземных линий метро создали в 1924 году, в Управлении московских городских дорог и трамвайной сети. И уже в 1932 году были развёрнуты подготовительные работы строительства столичного метро. Существует два способа прокладки линий метро - открытый и закрытый. Первый предусматривает проведение работ в котлованах с последующим засыпанием коммуникаций землёй, при втором способе применяют проходческие щиты и прокладываются они на больших глубинах. 

    Московская стройка предполагалась проводиться открытым способом, на небольших глубинах. С первых же недель работ открылось множество проблем: отсутствие квалифицированных специалистов, нехватка рабочих рук, трудности с жильём для приезжих рабочих. В феврале поступило предложение отказаться от стойки открытым методом и приступить к прокладке линий глубокого залегания от 20 до 35 м. И. В. Сталин поддержал это предложение, и строительство возобновилось на новом уровне. Но вместе с этим появились новые трудности. Во многих местах, глубже пятнадцати метров, вести подземные работы не позволяли грунтовые воды. В конечном итоге летом 1932 года было решено привлечь на постоянную работу семь иностранных специалистов и прокладку дорог на больших глубинах производить только в центральных частях столицы. К концу стройки количество рабочих, задействованных Мосметростроем, достигло 19 тысяч! 

Не менее масштабным и дорогостоящим был отделочный этап большой стройки. Специально для этих целей из Карелии, Кавказа, Урала и Крыма привозили тонны гранита и мрамора. Торжественное открытие метрополитена было назначено на 7 ноября 1934 года, но оно не состоялось. Причиной задержки стало отсутствие эскалаторов. В то время подвижные лестницы эксплуатировались лишь в Лондоне. За огромные деньги - 200 000 рублей золотом Мосметростроем был куплен за границей один экземпляр эскалатора, разобран и скопирован в более длинном варианте российскими конструкторами. В начале февраля 1935 года по новой железной дороге прошёл первый пилотный поезд, а над входами в метро появилась большая красная буква М, отныне ставшая символом Московского метрополитена. 

      </Div>
    </Group>
        </Panel>
    );
};
const mapProps = (state) => ({
    top: state.top
}); 
 
const mapDispatch = ({ navigator: { goBack },  navigator: { goForward }, transactions: { select, reset }, user: { load, openProfile }, cloud: { changeParam } }) => ({
	go: goForward,
	transactionSelect: select,
	resetTransactions: reset,
	loadUser: load,
    openProfile,
    back: goBack, 
	cloudUpdate: changeParam
});

export default connect(mapProps, mapDispatch)(Stor);